////////////////////////////////////////////////////////////////////////////////
// List Dropdown Layout
// -----------------------------------------------------------------------------
// Styles for add escort widget button on the admin page

.escort-item {

  .escort-list {
    list-style: none;
    margin: 0;
    padding: 0;

    ul {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    li {
      margin: 0;
      padding: 0;

      &:first-child {

        a {
          padding-top: $padding * 1.25;
          border-top: 0;
        }
      }

      &:last-child {

        a {
          padding-bottom: $padding * 1.25;
          border-bottom: 0;
        }
      }
    }

    a {
      display: block;
      text-align: left;
      padding: $padding;
    }
  }
}
